.Card {
  width: 80%;
  height: 80%;
  padding: 2rem;
  max-width: 25rem;
  border: 2px solid white;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 0.7rem;
  gap: 0.7rem;
  /* background-color: var(--color-neongreen); */
  box-shadow: 1px 1px 10px 2px #fff;
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  background-image: linear-gradient(to right, var(--color-orange) 50%, var(--color-neongreen) 50%);
  background-position: right bottom;
  background-size: 200% 100%;
  transition: all 0.3s ease-in-out;
}

.Card:hover {
  -webkit-transform: scale(1.06);
          transform: scale(1.06);
  box-shadow: 1px 1px 12px 5px #fff;
  background-position: left bottom;
}

.divider {
  width: 80%;
  height: 3px;
  background-color: white;
}

@media (min-width: 480px) {
  .Card {
    height: 20rem;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .Card {
    height: 25rem;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
}

.SearchForm {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 0.6rem;
  gap: 0.6rem;
}

.SearchForm-input {
  font-size: 0.8rem;
  padding: 0.5rem;
  font-family: inherit;
  border-radius: 5px;
  border: 1px solid white;
  width: 12rem;
  color: white;
  background-color: transparent;
  transition: -webkit-transform 0.1s ease-in-out;
  transition: transform 0.1s ease-in-out;
  transition: transform 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
  box-shadow: 1px 1px 5px #fff;
}

.SearchForm-input::-webkit-input-placeholder {
  text-align: center;
  color: white;
}

.SearchForm-input:-ms-input-placeholder {
  text-align: center;
  color: white;
}

.SearchForm-input::placeholder {
  text-align: center;
  color: white;
}

.SearchForm-input:focus {
  background-color: var(--color-orange);
  -webkit-transform: scale(1.07);
          transform: scale(1.07);
  outline: white;
}

.SearchForm-btn {
  font-family: inherit;
  width: 7rem;
  height: 2rem;
  text-align: center;
  background-color: transparent;
  color: white;
  outline: none;
  border: 2px solid var(--color-ivory);
  /* background-color: var(--color-neongreen); */
  border-radius: 5px;
  cursor: pointer;
  background-image: linear-gradient(135deg, var(--color-orange) 50%, var(--color-neongreen) 50%);
  background-position: top right;
  background-size: 230% 100%;
  transition: all 0.3s ease-in-out;
  box-shadow: 1px 1px 5px #fff;
}

.SearchForm-btn:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  background-position: top left;
}

/* media queries */
@media (min-width: 480px) {
  .SearchForm-btn {
    width: 10rem;
    height: 2.5rem;
    font-size: 1rem;
  }
  .SearchForm-input:focus {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
  }

  .SearchForm-input {
    font-size: 1rem;
    width: 22rem;
    height: 2.5rem;
  }
}
@media (min-width: 768px) {
  .SearchForm-btn {
    width: 11rem;
    height: 2.5rem;
    font-size: 1rem;
  }
  .SearchForm-input:focus {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
  }

  .SearchForm-input {
    font-size: 1rem;
    width: 22rem;
    height: 2.5rem;
  }
  .SearchForm {
    -webkit-flex-direction: row;
            flex-direction: row;
  }
}

